<template>
<div class="">
    <CCard class="pt-4" style="height:100%">
      <h1 style="color:#000080"> ATSC 3.0 over 5G</h1>
      <CCardBody>
        
        <CRow>
          <CCol md="2">

            <!-- <div v-for="(url, index) in urls" :key="index">
              <CButton block size="lg" variant="outline" color="info" @click="Play(url)"> 節目{{ index+1 }} </CButton>
            </div> -->

            <CButton block size="lg" variant="outline" color="info" @click="Play1">節目1</CButton>
            <CButton block size="lg" variant="outline" color="info" @click="Play2">節目2</CButton>
            <CButton block size="lg" variant="outline" color="info" @click="Play3">節目3</CButton>
            <CButton block size="lg" variant="outline" color="info" @click="Play4">節目4</CButton>
            <CButton block size="lg" variant="outline" color="info" @click="Play5">節目5</CButton>

          </CCol>
          <CCol md="10">
            <div v-show="show1">
              <video id="myMainVideoPlayer1" controls="true"></video>
            </div>
            <div v-show="show2">
              <video id="myMainVideoPlayer2" controls="true"></video>
            </div>
            <div v-show="show3">
              <video id="myMainVideoPlayer3" controls="true"></video>
            </div>
            <div v-show="show4">
              <video id="myMainVideoPlayer4" controls="true"></video>
            </div>
            <div v-show="show5">
              <video id="myMainVideoPlayer5" controls="true"></video>
            </div>
            
            
            
          </CCol>
        </CRow>
        <CRow>
          <CCol md="2"></CCol>
          <CCol md="10">
            <div class="mydiv p-3" style="border:1px solid black;">
              <h5>節目內容簡介</h5>
              <strong v-if="url">URL: {{ url }}</strong>
            </div>
          </CCol>
        </CRow>
      </CCardBody>
