<script>
import { MediaPlayer } from 'dashjs'

export default {
  data() {
    return {
      urls: [
        "https://dash.akamaized.net/envivio/Envivio-dash2/manifest.mpd",
        "https://dash.akamaized.net/dash264/TestCases/1c/qualcomm/2/MultiRate.mpd",
        "https://dash.akamaized.net/digitalprimates/fraunhofer/480p_video/heaac_2_0_with_video/Sintel/sintel_480p_heaac2_0.mpd",
        "https://storage.googleapis.com/shaka-demo-assets/tos-ttml/dash.mpd",
        "https://storage.googleapis.com/shaka-demo-assets/angel-one/dash.mpd",
        "https://dash.akamaized.net/envivio/Envivio-dash2/manifest.mpd",
        "https://dash.akamaized.net/dash264/TestCases/1c/qualcomm/2/MultiRate.mpd"
      ],
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      show5: false,
      url: ''
    }
  },
  created() {
    
  },
  methods: {
    Play(url) {
      // let url = "https://dash.akamaized.net/envivio/Envivio-dash2/manifest.mpd"
      let player = MediaPlayer().create()
      player.initialize(document.querySelector('#myMainVideoPlayer'), url, true)
    },
    Play1() {
      this.show1 = true
      this.show2 = false
      this.show3 = false
      this.show4 = false
      this.show5 = false
      let url = "https://dash.akamaized.net/envivio/Envivio-dash2/manifest.mpd"
      this.url = url
      let player = MediaPlayer().create()
      player.initialize(document.querySelector('#myMainVideoPlayer1'), url, true)
    },
    Play2() {
      this.show1 = false
      this.show2 = true
      this.show3 = false
      this.show4 = false
      this.show5 = false
      let url = "https://dash.akamaized.net/dash264/TestCases/1c/qualcomm/2/MultiRate.mpd"
      this.url = url
      let player = MediaPlayer().create()
      player.initialize(document.querySelector('#myMainVideoPlayer2'), url, true)
    },
    Play3() {
      this.show1 = false
      this.show2 = false
      this.show3 = true
      this.show4 = false
      this.show5 = false
      let url = "https://dash.akamaized.net/digitalprimates/fraunhofer/480p_video/heaac_2_0_with_video/Sintel/sintel_480p_heaac2_0.mpd"
      this.url = url
      let player = MediaPlayer().create()
      player.initialize(document.querySelector('#myMainVideoPlayer3'), url, true)
    },
    Play4() {
      this.show1 = false
      this.show2 = false
      this.show3 = false
      this.show4 = true
      this.show5 = false
      let url = "https://storage.googleapis.com/shaka-demo-assets/tos-ttml/dash.mpd"
      this.url = url
      let player = MediaPlayer().create()
      player.initialize(document.querySelector('#myMainVideoPlayer4'), url, true)
    },
    Play5() {
      this.show1 = false
      this.show2 = false
      this.show3 = false
      this.show4 = false
      this.show5 = true
      let url = "https://storage.googleapis.com/shaka-demo-assets/angel-one/dash.mpd"
      this.url = url
      let player = MediaPlayer().create()
      player.initialize(document.querySelector('#myMainVideoPlayer5'), url, true)
    },
    show() {
      this.show = !this.show
    }
  }
}
</script>

